<template>
	<view class="content">
		<view class="header" >
			<view class="input-view" >
				<input confirm-type="search" class="nav-bar-input" type="text" :disabled="true" placeholder="输入搜索关键词" @click="search">
				<view style="margin-top: 10rpx;" @click="camear">
					<u-icon name="camera" size="44" color="#666666"></u-icon>
				</view>
			</view>
			<view class="rig"   @click="go_pages('../information/information')">
				 <u-icon name="chat" size="50"></u-icon>消息
			</view>
		</view>
		<!-- //左侧 -->
		<scroll-view scroll-y class="left-aside">
			<view v-for="(item,ind) in flist" :key="ind" class="f-item b-b" :class="{active: currentId == item.id}" @click="tabtap(item.id,item.title)">
				{{item.title}}
			</view>
		</scroll-view>
		<!-- 右侧 -->
		<scroll-view scroll-with-animation scroll-y class="right-aside" :scroll-top="tabScrollTop">
			<!-- 轮播 -->
			<view class="" style="width: 98%;height: 230rpx;background-color: orange;">
				<swiper class="swiper" :indicator-dots="true" :autoplay="true" :circular="true" :interval="4000" :duration="500">
					<swiper-item v-for="(it,ind) in banners" :key="ind">
						<image :src="it.image" mode="aspectFill"></image>
					</swiper-item>
				</swiper>
			</view>
			<view class="one_list_sty"  v-if="tlist != ''">
				{{one_listname}}
			</view>
			<view class="t-list" v-if="tlist != ''">
				<view @click="navToList(titem.id)" class="t-item" v-for="titem in tlist" :key="titem.id">
					<image :src="titem.image"></image>
					<text>{{titem.title}}</text>
				</view>
			</view>
			<view v-else style="padding-top: 100rpx;">
				<u-empty text="暂无内容" mode="data"></u-empty>
			</view>
		</scroll-view>
		<!-- tabbar -->
		<zs-tabbar ></zs-tabbar>
		<!-- tabbar -->
	</view>
</template>

<script>
	import Json from '../../Json.js' //测试用数据
	export default {
		data() {
			return {
				sizeCalcState: false,
				tabScrollTop: 0,
				currentId: 1,
				flist: [],
				tlist: [],
				banners: [],
				one_listname:''
			}
		},
		onLoad(){
			this.loadData();
		},
		onReady() {
			uni.hideTabBar()
		},
		methods: {
			// 搜索
			search(){
				this.com.navto('../index/search')
			},
			//拍照
			camear(){
				uni.chooseImage({
				    count: 6, //默认9
				    sourceType: ['camera'], //从相册选择
				    success: function (res) {
				        console.log(JSON.stringify(res.tempFilePaths));
				    }
				}); 
			},
			//页面跳转
			go_pages(e){
				this.com.navto(e)
			},
			
			loadData(){
				this.$api.get('cate').then(res=>{
					console.log(res.data.label)
					this.flist = res.data.label
					
					this.tabtap(res.data.label[0].id,res.data.label[0].title)
				})
				//轮播
				this.$api.get('banner',{type:2}).then(res=>{
					console.log(res)
					if(res.status == 1){
						this.banners = res.data
					}
				})
			},
			//一级分类点击
			tabtap(i,name){
				this.currentId = i
				this.one_listname = name
				this.$api.get('cate',{label:i}).then(res=>{
					console.log(res)
					this.tlist = res.data.cate
				})
			},
			navToList(e){
				this.com.navto('./class_detail?classify_id='+e)
			}
		}
	}
</script>

<style lang='scss'>
	page,
	.content {
		height: 100%;
		background-color: #f8f8f8;
		display: flex;
		.header{
			position: fixed;left: 0;top: 0;
			height: 145rpx;width: 100%;
			padding-top: 55rpx;padding-left: 30rpx;
			display: flex;
			z-index: 999;background-color: white;
			.input-view {
				display: flex;
				flex-direction: row;justify-content: space-between;
				width: 68%;
				flex: 1;
				height: 60rpx;
				border-radius: 15px;
				padding: 0 15px;
				margin: 7px 0;
				line-height: 60rpx;
				background-color: #eee;
				
				input{
					width: 86%;
					margin-top: 10rpx;
					font-size: 30rpx;
					
				}
			}
			.rig{
				width: 20%;
				line-height: 100rpx;text-align: center;
				font-size: 30rpx;
			}
		}
	}
	.left-aside {
		flex-shrink: 0;
		width: 200upx;padding-bottom: 140rpx;padding-top: 160rpx;
		background-color: #fff;
	}
	.f-item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100upx;
		font-size: 28upx;
		
		position: relative;
		&.active{
			font-weight: bold;
			background: #f8f8f8;
			&:before{
				content: '';
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				height: 36upx;
				width: 7upx;
				background-color: #2d407a;
				opacity: .8;
			}
		}
	}

	.right-aside{
		width: 520rpx;
		flex: 1;
		overflow: hidden;
		padding-left: 20upx;
		padding-bottom: 140rpx;padding-top: 160rpx;
		.swiper{
			width: 100%;height: 100%;
			image{
				width: 100%;height: 100%;
			}
		}
	}
	.s-item{
		display: flex;
		align-items: center;
		height: 70upx;
		padding-top: 8upx;
		font-size: 28upx;
	}
	.one_list_sty{
		background-color: #fff;margin-top: 10rpx;
		padding-left: 20rpx;
	}
	.t-list{
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		background: #fff;
		
		padding-top: 20rpx;
		&:after{
			content: '';
			flex: 99;
			height: 0;
		}
	}
	.t-item{
		flex-shrink: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 32%;margin-right: 2%;
		font-size: 26upx;
		color: #666;
		padding-bottom: 20upx;
		
		image{
			width: 140upx;
			height: 140upx;
			background-color: #F1F1F1;
		}
	}
	.t-item:nth-child(3n+3){
		margin-right: 0;
	}
</style>
